























[v-cloak] {
  display: none;
}
.fade-enter {
  opacity: 1;
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.fade-leave {
  opacity: 0;
  -webkit-transform: translate3d(0,0,0);
          transform: translate3d(0,0,0);
}
.ui-view {
  min-width: 320px;
  max-width: 100%;
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}



